import { AccessControlList, TargetDrive } from '@youfoundation/dotyoucore-js';
import { useState } from 'react';
import { t } from '../../helpers/i18n/dictionary';
import useImage from '../../hooks/media/useImage';
import ConfirmDialog from '../Dialog/ConfirmDialog/ConfirmDialog';
import Exclamation from '../ui/Icons/Exclamation/Exclamation';
import Pencil from '../ui/Icons/Pencil/Pencil';
import Trash from '../ui/Icons/Trash/Trash';
import ImageDialog from '../Dialog/ImageDialog/ImageDialog';
import ErrorNotification from '../ui/Alerts/ErrorNotification/ErrorNotification';

interface ImageSelectorProps
  extends React.DetailedHTMLProps<React.InputHTMLAttributes<HTMLInputElement>, HTMLInputElement> {
  targetDrive?: TargetDrive;
  acl: AccessControlList;
  onChange: (event: { target: { name: string; value: string } }) => void;
}

const ImageSelector = ({ targetDrive, acl, onChange, defaultValue, name }: ImageSelectorProps) => {
  const {
    fetch: { data: imageUrl, isLoading },
    remove: { mutateAsync: removeImage, error: removeError },
  } = useImage(typeof defaultValue === 'string' ? defaultValue : undefined, targetDrive);
  const [isEdit, setIsEdit] = useState(false);
  const [needsConfirmation, setNeedsConfirmation] = useState(false);

  const removeData = async () => {
    await removeImage({
      fileId: typeof defaultValue === 'string' ? defaultValue : undefined,
      targetDrive: targetDrive,
    });
    onChange({ target: { name: name, value: undefined } });
  };

  if (isLoading && defaultValue) {
    return (
      <div className="aspect-square max-w-[20rem] animate-pulse bg-slate-100 dark:bg-slate-700"></div>
    );
  }

  return (
    <>
      {imageUrl ? (
        <div className="flex">
          <div className="relative mr-auto">
            <button
              className="absolute top-2 right-2 rounded-full bg-white p-2"
              onClick={(e) => {
                e.preventDefault();
                setIsEdit(true);
                return false;
              }}
            >
              <Pencil className="h-4 w-4 text-black" />
            </button>
            <button
              className="absolute bottom-2 right-2 rounded-full bg-red-200 p-2"
              onClick={() => {
                setNeedsConfirmation(true);
                return false;
              }}
            >
              <Trash className="h-4 w-4 text-black" />
            </button>
            <img
              src={imageUrl}
              alt={imageUrl}
              className="max-h-[20rem]"
              onClick={() => {
                setIsEdit(true);
              }}
            />
          </div>
        </div>
      ) : (
        <div
          className="relative flex aspect-square max-w-[20rem] cursor-pointer bg-slate-100 dark:bg-slate-700"
          onClick={(e) => {
            e.preventDefault();
            setIsEdit(true);
          }}
        >
          <Exclamation className="m-auto h-8 w-8" />
          <p className="absolute inset-0 top-auto pb-5 text-center text-slate-400">
            {t('No image selected')}
          </p>
        </div>
      )}

      <ImageDialog
        acl={acl}
        isOpen={isEdit}
        targetDrive={targetDrive}
        title={t('Insert image')}
        confirmText={t('Add')}
        onCancel={() => setIsEdit(false)}
        onConfirm={(imageFileId) => {
          onChange({ target: { name: name, value: imageFileId } });
          setIsEdit(false);
        }}
      />
      <ErrorNotification error={removeError} />
      <ConfirmDialog
        title="Remove Current Image"
        confirmText="Permanently remove"
        needConfirmation={needsConfirmation}
        onConfirm={removeData}
        onCancel={() => {
          setNeedsConfirmation(false);
        }}
      >
        <p className="text-sm text-gray-500">
          {t('Are you sure you want to remove the current file? This action cannot be undone.')}
        </p>
      </ConfirmDialog>
    </>
  );
};

export default ImageSelector;
