import { useEffect, useState } from 'react';
import { t } from '../../../helpers/i18n/dictionary';
import Alert from '../Alerts/Alert/Alert';

const seconds = 60000;
const minutes = 600000;

const SaveStatus = ({
  className,
  state,
  error,
}: {
  className: string;
  state: 'error' | 'idle' | 'loading' | 'success';
  error?: unknown;
}) => {
  const [lastSave, setLastSave] = useState<Date>(null);
  const [now, setNow] = useState<Date>(new Date());

  useEffect(() => {
    if (state === 'success') {
      setLastSave(new Date());
    }
  }, [state]);

  useEffect(() => {
    const timer = setTimeout(() => {
      setNow(new Date());
    }, 30000);

    return () => {
      clearTimeout(timer);
    };
  }, [now]);

  if (state === 'loading') {
    return <p className={`${className} text-sm`}>Saving...</p>;
  }

  if (state === 'error') {
    return (
      <Alert type="critical" className={className}>
        {error instanceof Error ? error.message : t('Something went wrong')}
      </Alert>
    );
  }

  if (!lastSave) {
    return null;
  }

  const diff = now.getTime() - lastSave.getTime();
  const text = lastSave
    ? diff <= seconds
      ? 'a few seconds ago'
      : diff <= minutes
      ? 'a few minutes ago'
      : lastSave.toLocaleString()
    : '';

  return text ? <p className={`${className} text-sm`}>Last saved {text}</p> : null;
};

export default SaveStatus;
