import { FC, ReactNode } from 'react';
import { Helmet } from 'react-helmet-async';
import { Link } from 'react-router-dom';
import SaveStatus from '../../Buttons/SaveStatus';

const PageMeta = ({
  title,
  actions,
  saveStatus,
  breadCrumbs,
  icon,
}: {
  title?: ReactNode | string;
  actions?: ReactNode;
  saveStatus?: 'error' | 'idle' | 'loading' | 'success';
  breadCrumbs?: { title: string; href?: string }[];
  icon?: FC;
}) => {
  return (
    <section
      className="-mx-5 -mt-4 mb-10 border-b-2 border-gray-100 bg-slate-50 py-4 pr-5 pl-10 dark:border-gray-700 dark:bg-slate-800 sm:-mx-10
    sm:-mt-8 sm:py-8 sm:px-10"
    >
      <div className="flex flex-col sm:flex-row">
        <div className="flex-col">
          {breadCrumbs && (
            <ul className="mb-2 hidden flex-row sm:flex">
              {breadCrumbs.map((crumb, index) => {
                return (
                  <li key={index} className="mr-2">
                    {crumb.href ? (
                      <Link to={crumb.href} className="">
                        {crumb.title}
                        <span className="ml-2">{'>'}</span>
                      </Link>
                    ) : (
                      <span className="text-slate-500">{crumb.title}</span>
                    )}
                  </li>
                );
              })}
            </ul>
          )}
          {title && (
            <>
              <h1 className="mb-5 flex flex-row text-4xl dark:text-white">
                {icon && icon({ className: 'h-8 w-8 my-auto mr-4' })} {title}
              </h1>
              <Helmet>
                <title>
                  {(typeof title !== 'object' && `${title}`) ||
                    window.location.pathname.split('/').pop()}{' '}
                  | Owner console
                </title>
              </Helmet>
            </>
          )}
        </div>
        <div className="ml-auto">
          <div className="-m-2 flex flex-col flex-wrap justify-end sm:flex-row sm:items-center">
            {actions}
          </div>
          {saveStatus && <SaveStatus className="mt-1" state={saveStatus} />}
        </div>
      </div>
    </section>
  );
};

export default PageMeta;
