import { useCallback, useMemo } from 'react';
import isHotkey from 'is-hotkey';
import {
  Editable,
  withReact,
  useSlate,
  Slate,
  ReactEditor,
  useSelected,
  useFocused,
  useSlateStatic,
} from 'slate-react';
import { Editor, Transforms, createEditor, Descendant, Element as SlateElement } from 'slate';
import { withHistory } from 'slate-history';

import { Button, Toolbar } from './RichTextEditorComponents';
import { debounce } from 'lodash-es';

import Bold from '../ui/Icons/RichTextEditorIcons/Bold/Bold';
import Italic from '../ui/Icons/RichTextEditorIcons/Italic/Italic';
import Underline from '../ui/Icons/RichTextEditorIcons/Underline/Underline';
import Quote from '../ui/Icons/RichTextEditorIcons/Quote/Quote';
import Code from '../ui/Icons/RichTextEditorIcons/Code/Code';
import Heading from '../ui/Icons/RichTextEditorIcons/Heading/Heading';
import OrderedList from '../ui/Icons/RichTextEditorIcons/OrderedList/OrderedList';
import UnorderedList from '../ui/Icons/RichTextEditorIcons/UnorderedList/UnorderedList';

import InsertImageButton from './InsertImageButton';
import { TargetDrive } from '@youfoundation/dotyoucore-js';
import Image from '../Image/Image';

import InsertLinkButton from './InsertLinkButton';
import ActionButton from '../ui/Buttons/ActionButton';
import { ellipsisAtMaxChar } from '../../helpers/common';

const HOTKEYS = {
  'mod+b': 'bold',
  'mod+i': 'italic',
  'mod+u': 'underline',
  'mod+`': 'code',
};

const LIST_TYPES = ['numbered-list', 'bulleted-list'];

const RichTextEditor = ({
  defaultValue,
  placeholder,
  mediaDrive,
  onChange,
}: {
  defaultValue: Descendant[];
  placeholder: string;
  mediaDrive: TargetDrive;
  onChange: (value: Descendant[]) => void;
}) => {
  const renderElement = useCallback((props) => <Element {...props} mediaDrive={mediaDrive} />, []);
  const renderLeaf = useCallback((props) => <Leaf {...props} />, []);
  const editor = useMemo(() => withHistory(withReact(createEditor() as ReactEditor)), []);

  const debouncedChange = useMemo(() => debounce(onChange, 1500), [onChange]);

  return (
    <section className="min-h-[30rem] w-[100%] overflow-hidden">
      <Slate editor={editor} value={defaultValue} onChange={debouncedChange}>
        <Toolbar>
          <MarkButton format="bold" icon={Bold} />
          <MarkButton format="italic" icon={Italic} />
          <MarkButton format="underline" icon={Underline} />
          <MarkButton format="code" icon={Code} />
          <BlockButton format="heading-one" icon={Heading} />
          <BlockButton format="heading-two" icon={Heading} />
          <BlockButton format="block-quote" icon={Quote} />
          <BlockButton format="numbered-list" icon={OrderedList} />
          <BlockButton format="bulleted-list" icon={UnorderedList} />
          <InsertImageButton targetDrive={mediaDrive} />
          <InsertLinkButton />
        </Toolbar>
        <Editable
          renderElement={renderElement}
          renderLeaf={renderLeaf}
          placeholder={placeholder}
          spellCheck
          autoFocus
          onKeyDown={(event) => {
            for (const hotkey in HOTKEYS) {
              if (isHotkey(hotkey, event)) {
                event.preventDefault();
                const mark = HOTKEYS[hotkey];
                toggleMark(editor, mark);
              }
            }
          }}
        />
      </Slate>
    </section>
  );
};
const toggleBlock = (editor, format) => {
  const isActive = isBlockActive(editor, format, 'type');
  const isList = LIST_TYPES.includes(format);

  Transforms.unwrapNodes(editor, {
    match: (n) => !Editor.isEditor(n) && SlateElement.isElement(n) && LIST_TYPES.includes(n.type),
    split: true,
  });
  const newProperties: Partial<SlateElement> = {
    type: isActive ? 'paragraph' : isList ? 'list-item' : format,
  };

  Transforms.setNodes<SlateElement>(editor, newProperties);

  if (!isActive && isList) {
    const block = { type: format, children: [] };
    Transforms.wrapNodes(editor, block);
  }
};

const toggleMark = (editor, format) => {
  const isActive = isMarkActive(editor, format);

  if (isActive) {
    Editor.removeMark(editor, format);
  } else {
    Editor.addMark(editor, format, true);
  }
};

const isBlockActive = (editor, format, blockType = 'type') => {
  const { selection } = editor;
  if (!selection) return false;

  const [match] = Array.from(
    Editor.nodes(editor, {
      at: Editor.unhangRange(editor, selection),
      match: (n) => !Editor.isEditor(n) && SlateElement.isElement(n) && n[blockType] === format,
    })
  );

  return !!match;
};

const isMarkActive = (editor, format) => {
  const marks = Editor.marks(editor);
  return marks ? marks[format] === true : false;
};
const Element = ({ attributes, children, element, mediaDrive }) => {
  const style = {};
  switch (element.type) {
    case 'block-quote':
      return (
        <blockquote style={style} {...attributes} className="border-l-4 pl-2">
          {children}
        </blockquote>
      );
    case 'bulleted-list':
      return (
        <ul style={style} {...attributes} className="list-disc pl-5">
          {children}
        </ul>
      );
    case 'heading-one':
      return (
        <h1 style={style} {...attributes} className={'text-2xl'}>
          {children}
        </h1>
      );
    case 'heading-two':
      return (
        <h2 style={style} {...attributes} className={'text-xl'}>
          {children}
        </h2>
      );
    case 'list-item':
      return (
        <li style={style} {...attributes}>
          {children}
        </li>
      );
    case 'numbered-list':
      return (
        <ol style={style} {...attributes} className="list-decimal pl-5">
          {children}
        </ol>
      );
    case 'image': {
      const editor = useSlateStatic();
      const path = ReactEditor.findPath(editor, element);

      const selected = useSelected();
      const focused = useFocused();

      return (
        <div {...attributes} className={`${attributes.className ?? ''} p-2`}>
          <div contentEditable={false}>
            <div className="my-5 flex flex-row">
              <div className="relative mr-auto">
                <div className="absolute right-1 top-1">
                  <ActionButton
                    onClick={() => {
                      // TODO: remove image from drive;
                      Transforms.removeNodes(editor, { at: path });
                    }}
                    type="remove"
                    icon="trash"
                    size="square"
                  />
                </div>
                <Image
                  targetDrive={mediaDrive} // TODO Add support for having the drive passed via props
                  fileId={element.imageFileId}
                  className={`max-w-md ${
                    selected && focused
                      ? 'outline outline-4 outline-offset-2 outline-indigo-500'
                      : ''
                  }`}
                />
              </div>
            </div>
          </div>
        </div>
      );
    }
    case 'link-button': {
      const editor = useSlateStatic();
      const path = ReactEditor.findPath(editor, element);

      return (
        <div {...attributes}>
          <div contentEditable={false}>
            <div className="my-5 flex flex-row">
              <a
                className="pointer-events-none rounded-md rounded-r-none bg-slate-500 px-3 py-2 text-left text-white"
                title={element.uri}
              >
                {element.linkText}
                <small className="block text-sm opacity-60">
                  {ellipsisAtMaxChar(element.uri, 60)}
                </small>
              </a>

              <ActionButton
                onClick={() => Transforms.removeNodes(editor, { at: path })}
                type="remove"
                icon="trash"
                size="square"
                className="rounded-l-none rounded-r-md"
              />
            </div>
          </div>
        </div>
      );
    }
    default:
      return (
        <p style={style} {...attributes}>
          {children}
        </p>
      );
  }
};

const Leaf = ({ attributes, children, leaf }) => {
  if (leaf.bold) {
    children = <strong>{children}</strong>;
  }

  if (leaf.code) {
    children = <code>{children}</code>;
  }

  if (leaf.italic) {
    children = <em>{children}</em>;
  }

  if (leaf.underline) {
    children = <u>{children}</u>;
  }

  return <span {...attributes}>{children}</span>;
};

const BlockButton = ({ format, icon: Icon }) => {
  const editor = useSlate();
  return (
    <Button
      active={isBlockActive(editor, format, 'type')}
      onMouseDown={(event) => {
        event.preventDefault();
        toggleBlock(editor, format);
      }}
    >
      {Icon && <Icon className="h-5 w-5" />}
    </Button>
  );
};

const MarkButton = ({ format, icon: Icon }) => {
  const editor = useSlate();
  return (
    <Button
      active={isMarkActive(editor, format)}
      onMouseDown={(event) => {
        event.preventDefault();
        toggleMark(editor, format);
      }}
    >
      {Icon && <Icon className="h-5 w-5" />}
    </Button>
  );
};

export default RichTextEditor;
