import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import {
  AccessControlList,
  ApiType,
  BlogConfig,
  Client,
  HomePageConfig,
  SecurityGroupType,
  TargetDrive,
} from '@youfoundation/dotyoucore-js';
import useAuth from '../auth/useAuth';

const defaultDrive: TargetDrive = {
  alias: HomePageConfig.BlogMainContentDriveId.toString(),
  type: BlogConfig.DriveType.toString(),
};

const useImage = (imageFileId?: string, imageDrive?: TargetDrive) => {
  const { getSharedSecret } = useAuth();
  const queryClient = useQueryClient();
  const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

  const fetchImageData = async (imageFileId?: string, imageDrive?: TargetDrive) => {
    if (imageFileId === undefined) {
      return;
    }

    return await client.mediaProvider.getDecryptedImageUrl(imageDrive ?? defaultDrive, imageFileId);
  };

  const saveImage = async ({
    bytes,
    type,
    targetDrive = defaultDrive,
    acl = { requiredSecurityGroup: SecurityGroupType.Anonymous },
    fileId = undefined,
  }: {
    bytes: Uint8Array;
    type: string;
    targetDrive: TargetDrive;
    acl?: AccessControlList;
    fileId?: string;
  }) => {
    return await client.mediaProvider.uploadImage(targetDrive, undefined, acl, bytes, fileId, type);
  };

  const removeImage = async ({
    targetDrive = defaultDrive,
    fileId,
  }: {
    targetDrive: TargetDrive;
    fileId: string;
  }) => {
    return await client.mediaProvider.removeImage(fileId, targetDrive);
  };

  return {
    fetch: useQuery(
      ['image', imageFileId, imageDrive],
      () => fetchImageData(imageFileId, imageDrive),
      {
        refetchOnMount: false,
        refetchOnWindowFocus: false,
        staleTime: Infinity,
        enabled: !!imageFileId,
      }
    ),
    save: useMutation(saveImage, {
      onSuccess: (_data, variables) => {
        // Boom baby!
        if (variables.fileId) {
          queryClient.invalidateQueries([
            'image',
            variables.fileId,
            variables.targetDrive ?? defaultDrive,
          ]);
        } else {
          queryClient.removeQueries(['image']);
        }
      },
    }),
    remove: useMutation(removeImage),
    // The remove mutation doesn't force invalidate the cache anymore, as removing an image always corresponds to removing the refrence as well.
  };
};

export default useImage;
