import { DataUtil, PostContent, PostFile, SecurityGroupType } from '@youfoundation/dotyoucore-js';
import { useNavigate, useParams } from 'react-router-dom';
import { ellipsisAtMaxChar, makeSlug } from '../../../helpers/common';
import { t } from '../../../helpers/i18n/dictionary';
import usePost from '../../../hooks/posts/usePost';
import useChannel from '../../../hooks/posts/useChannel';
import ErrorNotification from '../../../components/ui/Alerts/ErrorNotification/ErrorNotification';
import PostEditor from '../../../components/Blog/PostEditor/PostEditor';
import ActionButton from '../../../components/ui/Buttons/ActionButton';
import PageMeta from '../../../components/ui/Layout/PageMeta/PageMeta';
import LoadingDetailPage from '../../../components/ui/Loaders/LoadingDetailPage/LoadingDetailPage';
import { useState, useEffect } from 'react';

const PostDetail = () => {
  const navigate = useNavigate();
  const { channelKey, blogKey } = useParams();
  const { data: channel } = useChannel({
    channelSlug: channelKey,
  }).fetch;

  const {
    fetch: { data: serverPost, isLoading: isBlogLoading, isFetched: isBlogFetched },
    save: { mutate: savePost, status: savePostStatus, error: savePostError },
    remove: { mutate: removePost, error: removePostError },
  } = usePost({
    channelSlug: channelKey,
    blogSlug: blogKey,
  });

  const [newPost, setNewPost] = useState<PostFile<PostContent>>(
    blogKey === 'new'
      ? {
          acl: { requiredSecurityGroup: SecurityGroupType.Owner },
          content: {
            id: DataUtil.getNewId(),
            channelId: channel?.channelId ?? undefined,
            caption: '',
            slug: '',
            dateUnixTime: new Date().getTime(),
            type: 'Article',
            primaryImageFileId: '',
          },
        }
      : undefined
  );

  // If there is a blog coming from serverSide, we want to use this as the "in-component state"
  useEffect(() => {
    if (isBlogFetched && serverPost) {
      setNewPost({ ...serverPost });
    }
  }, [serverPost]);

  // Channel might load after the new empty post has been setup as blogKey === 'new' will match faster then getting the channel/post;
  useEffect(() => {
    if (channel && newPost?.content) {
      setNewPost({ ...newPost, content: { ...newPost.content, channelId: channel.channelId } });
    }
  }, [channel]);

  const savePostFile = (postFile: PostFile<PostContent>) => {
    if (!postFile.content.slug) {
      postFile.content.slug = makeSlug(postFile);
    }

    if (!postFile.content.caption) {
      postFile.content.caption = t('Untitled');
    }

    setNewPost({ ...postFile });
    savePost(
      { blogFile: postFile, channelId: postFile.content.channelId },
      {
        onSuccess: (serverFileId) => {
          // When content slug isn't equal to the current blogKey, update browser with that slug, to avoid losing context
          if (!newPost || (blogKey !== postFile.content.slug && blogKey !== postFile.fileId)) {
            const path = window.location.pathname.split('/');
            path.pop();
            if (path) {
              window.history.replaceState(
                null,
                postFile.content.caption,
                `${path.join('/')}/${postFile.content.slug}`
              );
            }
          }

          // Update in component state again to keep fileId linked
          setNewPost({ ...postFile, fileId: serverFileId });
        },
      }
    );
  };

  const makePublic = () => {
    const dirtyBlog: PostFile<PostContent> = { ...newPost };
    dirtyBlog.acl = { ...channel.acl };
    dirtyBlog.content.dateUnixTime = new Date().getTime();

    savePostFile(dirtyBlog);
  };

  const makeDraft = () => {
    const dirtyBlog: PostFile<PostContent> = { ...newPost };
    dirtyBlog.acl = { requiredSecurityGroup: SecurityGroupType.Owner };

    savePostFile(dirtyBlog);
  };

  if (isBlogLoading) {
    return <LoadingDetailPage />;
  }

  return (
    <>
      <ErrorNotification error={savePostError} />
      <ErrorNotification error={removePostError} />
      <section>
        <PageMeta
          title={
            newPost?.content?.caption
              ? ellipsisAtMaxChar(newPost.content.caption, 50)
              : t('New post')
          }
          actions={
            <>
              <ActionButton
                type="remove"
                className="m-2"
                icon="trash"
                confirmOptions={{
                  title: `${t('Remove')} ${newPost?.content.caption.substring(0, 50) ?? ''}`,
                  buttonText: 'Permanently remove',
                  body: t(
                    'Are you sure you want to remove this post? This action cannot be undone.'
                  ),
                }}
                onClick={() =>
                  removePost(
                    {
                      fileId: newPost.fileId,
                      channelId: newPost.content.channelId,
                      slug: newPost.content.slug,
                    },
                    {
                      onSuccess: () => {
                        const path = window.location.pathname.split('/');
                        path.pop();
                        if (path) {
                          navigate(`${path.join('/')}`);
                        }
                      },
                    }
                  )
                }
              />
              {!newPost?.acl ||
              newPost.acl.requiredSecurityGroup?.toLowerCase() ===
                SecurityGroupType.Owner.toLowerCase() ? (
                <ActionButton
                  type="primary"
                  className="m-2"
                  onClick={makePublic}
                  confirmOptions={{
                    title: t('Publish'),
                    body: t(
                      'Are you sure you want to publish this post, the contents will be accessible as specified on the channel'
                    ),
                    buttonText: t('Publish'),
                  }}
                >
                  {t('Publish')}
                </ActionButton>
              ) : (
                <ActionButton
                  type="primary"
                  className="m-2"
                  onClick={makeDraft}
                  confirmOptions={{
                    title: t('Unpublish'),
                    body: t(
                      'Are you sure you want to unpublish this post, it will again become a draft'
                    ),
                    buttonText: t('Unpublish'),
                  }}
                >
                  {t('Unpublish')}
                </ActionButton>
              )}
            </>
          }
          breadCrumbs={[
            { href: '/owner/posts', title: 'Posts' },
            { href: `/owner/posts/${channel?.slug}`, title: channel.name },
            { title: newPost?.content?.caption ?? t('New') },
          ]}
          saveStatus={savePostStatus}
        />

        <div className="md:mx-auto md:max-w-[40rem]">
          {newPost && <PostEditor post={newPost} savePost={savePostFile} />}
        </div>
      </section>
    </>
  );
};

export default PostDetail;
