import {
  ProviderOptions,
  BlogDefinitionProvider,
  BlogPostReadonlyProvider,
  DriveProvider,
  PostContent,
  PostFile,
  DataUtil,
  SecurityGroupType,
  UploadInstructionSet,
  UploadFileMetadata,
  postTypeToTag,
  BlogConfig,
  UploadResult,
  ApiType,
} from '@youfoundation/dotyoucore-js';

interface BlostPostProviderOptions extends ProviderOptions {
  driveProvider: DriveProvider;
  blogDefinitionProvider: BlogDefinitionProvider;
}

export default class BlogPostProvider extends BlogPostReadonlyProvider {
  private static instance: BlogPostProvider;

  constructor(options: BlostPostProviderOptions) {
    super({
      api: options.api,
      sharedSecret: options.sharedSecret,
      driveProvider: options.driveProvider,
      blogDefinitionProvider: options.blogDefinitionProvider,
    });
  }

  async savePost<T extends PostContent>(file: PostFile<T>, channelId: string): Promise<string> {
    if (!file.content.id) {
      file.content.id = file.content.slug
        ? DataUtil.toGuidId(file.content.slug)
        : DataUtil.getNewId();
    } else if (!file.fileId) {
      // Check if content.id exists and with which fileId
      file.fileId = (await this.getPost(channelId, file.content.id))?.fileId ?? undefined;
    }

    const encrypt = !(
      file.acl?.requiredSecurityGroup === SecurityGroupType.Anonymous ||
      file.acl?.requiredSecurityGroup === SecurityGroupType.Authenticated
    );

    const instructionSet: UploadInstructionSet = {
      transferIv: this._driveProvider.Random16(),
      storageOptions: {
        overwriteFileId: file?.fileId ?? '',
        drive: this._blogDefinitionProvider.getTargetDrive(channelId),
      },
      transitOptions: null,
    };

    const payloadJson: string = DataUtil.JsonStringify64(file.content);
    const payloadBytes = DataUtil.stringToUint8Array(payloadJson);

    const uniqueId = file.content.slug ? DataUtil.toGuidId(file.content.slug) : file.content.id;

    // Set max of 3kb for jsonContent so enough room is left for metedata
    const shouldEmbedContent = payloadBytes.length < 3000;
    const metadata: UploadFileMetadata = {
      contentType: 'application/json',
      appData: {
        tags: [
          postTypeToTag('type' in file.content ? file.content.type : 'Article').toString(),
          file.content.id,
          ...(uniqueId && uniqueId !== file.content.id ? [uniqueId] : []),
        ],
        uniqueId: uniqueId,
        contentIsComplete: shouldEmbedContent,
        fileType: BlogConfig.PostFileType,
        jsonContent: shouldEmbedContent ? payloadJson : null,
      },
      payloadIsEncrypted: encrypt,
      accessControlList: file.acl,
    };

    const result: UploadResult = await this._driveProvider.Upload(
      instructionSet,
      metadata,
      payloadBytes,
      undefined,
      encrypt
    );

    return result.file.fileId;
  }

  async removePost(fileId: string, channelId: string) {
    const targetDrive = this._blogDefinitionProvider.getTargetDrive(channelId);
    this._driveProvider.DeleteFile(targetDrive, fileId);
  }

  public static getInstance(sharedSecret?: Uint8Array): BlogPostProvider {
    if (!sharedSecret) {
      throw new Error('Shared Secret not set for OwnerProvider');
    }

    if (!BlogPostProvider.instance) {
      const driveProvider = new DriveProvider({ api: ApiType.Owner, sharedSecret: sharedSecret });
      const blogDefinitionProvider = new BlogDefinitionProvider({
        api: ApiType.Owner,
        sharedSecret: sharedSecret,
        driveProvider: driveProvider,
      });

      BlogPostProvider.instance = new BlogPostProvider({
        api: ApiType.Owner,
        sharedSecret: sharedSecret,
        driveProvider: driveProvider,

        blogDefinitionProvider: blogDefinitionProvider,
      });
    }

    return BlogPostProvider.instance;
  }
}
