import { useState } from 'react';
import { Node, Transforms } from 'slate';
import { useSlateStatic } from 'slate-react';
import ImageDialog from '../Dialog/ImageDialog/ImageDialog';
import { CustomEditor, ImageElement } from './rich-text-editor';
import { Button } from './RichTextEditorComponents';
import Image from '../ui/Icons/RichTextEditorIcons/Image/Image';
import { SecurityGroupType, TargetDrive } from '@youfoundation/dotyoucore-js';
import { t } from '../../helpers/i18n/dictionary';

const insertImage = (editor: CustomEditor, imageFileId: string) => {
  const text = { text: '' };
  // https://stackoverflow.com/questions/61997167/add-paragraph-after-image-within-a-slate-react-text-editor
  // const image: ImageElement = { type: 'image', imageFileId, children: [text] };
  const image = [
    { type: 'image', imageFileId, children: [text] } as ImageElement,
    {
      type: 'paragraph',
      children: [text],
    } as Node,
  ];

  Transforms.insertNodes(editor, image);
};

const InsertImageButton = ({ targetDrive }: { targetDrive: TargetDrive }) => {
  const [isActive, setIsActive] = useState(false);
  const editor = useSlateStatic();

  return (
    <>
      <Button
        onClick={(e) => {
          e.preventDefault();
          setIsActive(true);
        }}
      >
        <Image className="h-5 w-5" />
      </Button>
      <ImageDialog
        isOpen={isActive}
        onCancel={() => {
          setIsActive(false);
        }}
        onConfirm={(imageFileId) => {
          insertImage(editor, imageFileId);
          setIsActive(false);
        }}
        title={t('Insert image')}
        confirmText={t('Add')}
        acl={{ requiredSecurityGroup: SecurityGroupType.Anonymous }}
        targetDrive={targetDrive} // TODO Add support for having the drive passed via props
      />
    </>
  );
};

export default InsertImageButton;
