import {
  Article,
  PostContent,
  SecurityGroupType,
  getChannelDrive,
  Video,
  TargetDrive,
} from '@youfoundation/dotyoucore-js';
import { debounce } from 'lodash-es';
import { useMemo } from 'react';
import { Descendant } from 'slate';
import { t } from '../../../helpers/i18n/dictionary';
import ImageSelector from '../../Form/ImageSelector';
import Input from '../../Form/Input';
import Label from '../../Form/Label';
import Select from '../../Form/Select';
import Textarea from '../../Form/Textarea';
import { ErrorBoundary } from '../../ui/Layout/ErrorBoundary/ErrorBoundary';
import RichTextEditor from '../../RichTextEditor/RichTextEditor';

const PostFieldsEditor = ({
  post,
  mediaDrive,
  onChange,
}: {
  post: PostContent;
  mediaDrive: TargetDrive;
  onChange: (event: { target: { name: string; value: string | Descendant[] } }) => void;
}) => {
  const debouncedChange = useMemo(() => debounce(onChange, 500), [onChange]);

  let typeSpecificFields = <></>;
  if (post.type === 'Article') {
    const article = post as Article;

    const body: Descendant[] = Array.isArray(article?.body)
      ? (article?.body as Descendant[])
      : [
          {
            type: 'paragraph',
            children: [{ text: article?.body ?? '' }],
          },
        ];

    typeSpecificFields = (
      <>
        <div className="mb-5">
          <Label className="mb-2 block" htmlFor="post_abstract">
            {t('Abstract')}
          </Label>
          <Textarea
            id="post_abstract"
            name="abstract"
            defaultValue={article.abstract}
            onChange={debouncedChange}
          />
        </div>
        <ErrorBoundary>
          <RichTextEditor
            defaultValue={body}
            placeholder={t('Start writing...')}
            mediaDrive={mediaDrive}
            onChange={(value) => {
              onChange({ target: { name: 'body', value: value } });
            }}
          />
        </ErrorBoundary>
      </>
    );
  } else if (post.type === 'Video') {
    const video = post as Video;

    typeSpecificFields = (
      <>
        <div className="mb-5">
          <Label className="mb-2 block" htmlFor="post_videoId">
            {t('Video File Id')}
          </Label>
          <Input
            id="post_videoId"
            name="videoFileId"
            defaultValue={video.videoFileId}
            onChange={debouncedChange}
          />
        </div>
      </>
    );
  }

  return (
    <>
      <div className="mb-5">
        <Label className="mb-2 block" htmlFor="post_type">
          {t('Type')}
        </Label>
        <Select id="post_type" name="type" defaultValue={post.type} onChange={debouncedChange}>
          <option value={'Article'}>Article</option>
          <option value={'Image'}>Image</option>
          <option value={'Video'}>Video</option>
          <option value={'Tweet'}>Tweet</option>
        </Select>
      </div>
      <div className="mb-5">
        <Label className="mb-2 block" htmlFor="post_caption">
          {t('Caption')}
        </Label>
        <Input
          id="post_caption"
          name="caption"
          defaultValue={post.caption}
          onChange={debouncedChange}
        />
      </div>

      <div className="mb-5">
        <Label className="mb-2 block" htmlFor="post_image">
          {t('Primary image')}
        </Label>
        <ImageSelector
          id="post_image"
          name="primaryImageFileId"
          defaultValue={post.primaryImageFileId}
          onChange={debouncedChange}
          targetDrive={getChannelDrive(post.channelId)}
          acl={{ requiredSecurityGroup: SecurityGroupType.Anonymous }}
        />
      </div>

      {post.slug ? (
        <div className="mb-5">
          <Label className="mb-2 block" htmlFor="post_slug">
            {t('Slug')}
          </Label>
          <Input
            id="post_slug"
            name="slug"
            defaultValue={post.slug}
            disabled
            onChange={debouncedChange}
          />
        </div>
      ) : null}

      {typeSpecificFields}
    </>
  );
};

export default PostFieldsEditor;
