import { getChannelDrive, PostContent, PostFile } from '@youfoundation/dotyoucore-js';
import { Descendant } from 'slate';
import { makeSlug } from '../../../helpers/common';
import PostFieldsEditor from '../PostFieldsEditor/PostFieldsEditor';

const PostEditor = ({
  post,
  savePost,
}: {
  post: PostFile<PostContent>;
  savePost: (blogFile: PostFile<PostContent>) => void;
}) => {
  const onChange = (e: { target: { name: string; value: string | Descendant[] } }) => {
    const dirtyData = { ...post };

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    (dirtyData.content as any)[e.target.name] = e.target.value;

    if (e.target.name === 'type') {
      dirtyData.content.slug = makeSlug(dirtyData);
    }

    savePost(dirtyData);
  };

  return (
    <PostFieldsEditor
      post={post.content}
      mediaDrive={getChannelDrive(post.content?.channelId)}
      onChange={onChange}
    />
  );
};

export default PostEditor;
