import { useState } from 'react';
import { Transforms } from 'slate';
import { useSlateStatic } from 'slate-react';
import { CustomEditor, LinkButtonElement } from './rich-text-editor';
import { Button } from './RichTextEditorComponents';
import { t } from '../../helpers/i18n/dictionary';
import LinkButton from "../Icons/RichTextEditorIcons/LinkButton/LinkButton";
import LinkButtonDialog, { LinkButtonProps } from "../Dialog/LinkButtonDialog/LinkButtonDialog";

const insertButton = (editor: CustomEditor, buttonProps: LinkButtonProps) => {
  const text = {text: ''};
  const button: LinkButtonElement = {type: 'link-button', ...buttonProps, children: [text]};
  Transforms.insertNodes(editor, button);
};

const InsertLinkButton = () => {
  const [isActive, setIsActive] = useState(false);
  const editor = useSlateStatic();

  return (
    <>
      <Button
        onClick={(e) => {
          e.preventDefault();
          setIsActive(true);
        }}
      >
        <LinkButton className="h-5 w-5"/>
      </Button>
      <LinkButtonDialog
        isOpen={isActive}
        onCancel={() => {
          setIsActive(false);
        }}
        onConfirm={(props) => {
          insertButton(editor, props);
          setIsActive(false);
        }}
        title={t('Include link button')}
        confirmText={t('Save')}
      />
    </>
  );
};

export default InsertLinkButton;
