import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { ApiType, Client, DataUtil, PostContent, PostFile } from '@youfoundation/dotyoucore-js';
import BlogPostProvider from '../../provider/blog/BlogPostProvider';
import useAuth from '../auth/useAuth';
import useStaticFiles from '../staticFiles/useStaticFiles';

type usePostProps = {
  channelSlug?: string;
  blogSlug?: string;
};

const usePost = ({ channelSlug, blogSlug }: usePostProps = {}) => {
  const { getSharedSecret } = useAuth();
  const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });
  const blogPostProvider = BlogPostProvider.getInstance(getSharedSecret());

  const queryClient = useQueryClient();
  const { mutate: publishStaticFiles } = useStaticFiles().publish;

  const fetchBlog = async ({ blogSlug, channelSlug }: usePostProps) => {
    if (!blogSlug) {
      return;
    }

    const channel = await client.blogDefinitionProvider.getChannelDefinitionBySlug(channelSlug);
    const directMatch = await blogPostProvider.getPost(
      channel.channelId,
      DataUtil.toGuidId(blogSlug)
    );
    if (directMatch) {
      return directMatch;
    }

    // Nothing found...
    return;
  };

  const saveData = async ({
    blogFile,
    channelId,
  }: {
    blogFile: PostFile<PostContent>;
    channelId: string;
  }) => {
    return await blogPostProvider.savePost(blogFile, channelId);
  };

  // slug property is need to clear the cache later, but not for the actual removeData
  const removeData = async ({
    fileId,
    channelId,
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    slug,
  }: {
    fileId: string;
    channelId: string;
    slug: string;
  }) => {
    return await blogPostProvider.removePost(fileId, channelId);
  };

  return {
    fetch: useQuery(['blog', channelSlug, blogSlug], () => fetchBlog({ blogSlug, channelSlug }), {
      refetchOnWindowFocus: false,
    }),
    save: useMutation(saveData, {
      onSuccess: (_data, variables) => {
        if (variables.blogFile.content.slug) {
          queryClient.invalidateQueries(['blog', variables.blogFile.content.slug]);
        } else {
          queryClient.invalidateQueries(['blog']);
        }
        queryClient.removeQueries(['blogs']);

        publishStaticFiles();
      },
    }),
    remove: useMutation(removeData, {
      onSuccess: (_data, variables) => {
        if (variables && variables.slug) {
          queryClient.invalidateQueries(['blog', variables.slug]);
        } else {
          queryClient.invalidateQueries(['blog']);
        }
        queryClient.removeQueries(['blogs']);
      },
    }),
  };
};

export default usePost;
