import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { ApiType, ChannelDefinition, Client } from '@youfoundation/dotyoucore-js';
import useAuth from '../auth/useAuth';
import useStaticFiles from '../staticFiles/useStaticFiles';

const useChannel = ({
  channelId,
  channelSlug,
}: { channelId?: string; channelSlug?: string } = {}) => {
  const { getSharedSecret } = useAuth();
  const client = new Client({ api: ApiType.Owner, sharedSecret: getSharedSecret() });

  const queryClient = useQueryClient();
  const { mutate: publishStaticFiles } = useStaticFiles().publish;

  const fetchData = async (channelId?: string, channelSlug?: string) => {
    if (channelId) {
      return await client.blogDefinitionProvider.getChannelDefinition(channelId);
    }
    if (channelSlug) {
      return await client.blogDefinitionProvider.getChannelDefinitionBySlug(channelSlug);
    }
  };

  const saveData = async (channelDef: ChannelDefinition) => {
    await client.blogDefinitionProvider.saveChannelDefinition(channelDef);
  };

  const removeChannel = async (channelDef: ChannelDefinition) => {
    await client.blogDefinitionProvider.removeChannelDefinition(channelDef.channelId);
  };

  return {
    fetch: useQuery(
      ['channel', channelId ?? channelSlug],
      () => fetchData(channelId, channelSlug),
      {
        refetchOnWindowFocus: false,
      }
    ),
    save: useMutation(saveData, {
      onSuccess: (_data, variables) => {
        // Boom baby!
        if (variables.channelId) {
          queryClient.removeQueries(['channel', variables.channelId]);
        } else {
          queryClient.removeQueries(['channel']);
        }
        queryClient.removeQueries(['channels']);

        publishStaticFiles();
      },
    }),
    remove: useMutation(removeChannel, {
      onMutate: async (toRemoveChannel) => {
        await queryClient.cancelQueries(['channels']);

        const previousChannels: ChannelDefinition[] = queryClient.getQueryData(['channels']);
        const newChannels = previousChannels?.filter(
          (channel) => channel.channelId !== toRemoveChannel.channelId
        );

        queryClient.setQueryData(['channels'], newChannels);

        return { previousChannels, toRemoveChannel };
      },
      onError: (err, newData, context) => {
        console.error(err);

        queryClient.setQueryData(['channels'], context.previousChannels);
      },
      onSettled: () => {
        queryClient.invalidateQueries(['channels']);
      },
    }),
  };
};

export default useChannel;
